
import { defineComponent, ref, watch } from "vue";

import CustomRouterLink from "@/components/CustomRouterLink.vue";
export default defineComponent({
  name: "mobileelement",
  components: {
    CustomRouterLink,
  },
  props: {
    isMenuOpen: {
      required: true,
      type: Boolean,
    },
  },
  setup(props, { emit }) {
    const handleMenuItemClick = () => {
      emit("closeMenu");
    };
    return { handleMenuItemClick };
  },
});
