<template>
  <div class="relative overflow-hidden wrapper">
    <div
      id="skillcloud"
      class="absolute w-full h-full"
      @pointerdown="handleInteraction"
      @pointerup="handleInteraction"
    ></div>
  </div>
</template>

<script>
import { PerspectiveCamera, Scene, Vector3, Group } from "three";
import { TrackballControls } from "/node_modules/three/examples/jsm/controls/TrackballControls.js";
import {
  CSS2DRenderer,
  CSS2DObject,
} from "/node_modules/three/examples/jsm/renderers/CSS2DRenderer.js";
import { skills } from "../assets/data/skillBall";
import { defineComponent, ref, onMounted, onUnmounted } from "vue";
export default defineComponent({
  setup() {
    const skillList = ref();
    const objects = ref();
    const container = ref();
    const camera = ref();
    const scene = ref();
    const renderer = ref();
    const group = ref();
    const controls = ref();
    const hasInteraction = ref();

    const init = () => {
      skillList.value = skills;
      objects.value = [];
      container.value = document.getElementById("skillcloud");
      camera.value = new PerspectiveCamera(35, 1, 0.1, 3500);
      camera.value.position.z = 2000;
      scene.value = new Scene();
      renderer.value = new CSS2DRenderer();
      renderer.value.setSize(
        container.value.parentElement.clientWidth,
        container.value.parentElement.clientHeight
      );
      group.value = new Group();
      scene.value.add(group.value);
      addSphereToScene();
      container.value.appendChild(renderer.value.domElement);

      controls.value = new TrackballControls(
        camera.value,
        renderer.value.domElement
      );
      controls.value.rotateSpeed = 2;
      controls.value.noPan = true;
      controls.value.noZoom = true;
      hasInteraction.value = false;
    };

    const addSphereToScene = () => {
      const vector = new Vector3();
      const fog = document.createElement("div");
      fog.className = "fog";
      const fogObject = new CSS2DObject(fog);
      scene.value.add(fogObject);
      for (let i = 0; i < skillList.value.length; i++) {
        // Create a wrapper element
        const iconWrapper = document.createElement("div");
        iconWrapper.className = "iconWrapper";

        // Create and add icon as an image to the element
        const img = document.createElement("img");
        img.className = "icon";
        img.src =
          "https://unpkg.com/simple-icons@v5/icons/" +
          skillList.value[i].slug +
          ".svg";
        img.alt = skillList.value[i].name;
        img.draggable = false;

        img.style.filter =
          "invert(64%) sepia(6%) saturate(1740%) hue-rotate(185deg) brightness(87%) contrast(83%)";

        const label = document.createElement("p");
        label.className = "label";
        label.innerText = skillList.value[i].name;

        iconWrapper.appendChild(img);
        iconWrapper.appendChild(label);

        const objectCSS = new CSS2DObject(iconWrapper);

        const phi = Math.acos(-1 + (2 * i) / skillList.value.length);
        const theta = Math.sqrt(skillList.value.length * Math.PI) * phi;
        objectCSS.position.setFromSphericalCoords(500, phi, theta);
        vector.copy(objectCSS.position).multiplyScalar(2);

        // collect final objects to manipulate opacity later
        objects.value.push(objectCSS);
        group.value.add(objectCSS);
      }
    };

    const animate = () => {
      requestAnimationFrame(animate);
      addOpacity();
      if (!hasInteraction.value) {
        group.value.rotation.y += 0.004;
        group.value.rotation.x += 0.004;
      }
      render();
      controls.value.update();
    };

    const addOpacity = () => {
      for (let i = 0; i < objects.value.length; i++) {
        objects.value[i].element.style.opacity =
          (objects.value[i].element.style.zIndex - 1) /
          (objects.value.length - 1);
      }
    };
    const render = () => {
      renderer.value.render(scene.value, camera.value);
    };
    const handleInteraction = (e) => {
      if (!e) return;
      if (e.type == "pointerdown") {
        hasInteraction.value = true;
      }
      if (e.type == "pointerup") {
        hasInteraction.value = false;
      }
    };
    onMounted(() => {
      const windowResizeHandler = () => {
        camera.value.aspect =
          container.value.parentElement.clientWidth /
          container.value.parentElement.clientHeight;
        camera.value.updateProjectionMatrix();
        renderer.value.setSize(
          container.value.parentElement.clientWidth,
          container.value.parentElement.clientHeight
        );
        controls.value.handleResize();
        render();
      };
      window.addEventListener("resize", windowResizeHandler);
      init();
      animate();
    });
    onUnmounted(() => {
      const windowResizeHandler = () => {
        camera.value.aspect =
          container.value.parentElement.clientWidth /
          container.value.parentElement.clientHeight;
        camera.value.updateProjectionMatrix();
        renderer.value.setSize(
          container.value.parentElement.clientWidth,
          container.value.parentElement.clientHeight
        );
        controls.value.handleResize();
        render();
      };
      window.removeEventListener("resize", windowResizeHandler);
    });
    return { handleInteraction };
  },
});
</script>

<style>
.wrapper {
  aspect-ratio: 1/1;
  width: 80vmin;
  height: 80vmin;
  max-width: 576px;
  max-height: 576px;
  margin: auto;
  flex: 0 1 auto;
}
.fog {
  background: #0e16301a;
  width: 100%;
  height: 100%;
}
.iconWrapper {
  width: 60px;
  height: 60px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 5px;
  border-radius: 50%;
}
.icon {
  user-select: none;
}
.label {
  user-select: none;
  color: #fff;
}
@media screen and (max-width: 768px) {
  .icon {
    width: 25px;
    height: 25px;
  }
  .label {
    font-size: 0.6rem;
  }
}
@media screen and (min-width: 768px) {
  .icon {
    width: 50px;
    height: 50px;
  }
  .label {
    font-size: 0.8rem;
  }
}
</style>
